@import '../../assets/styles/_variables';

$turquoise: #1abc9c;
$greensea: #16a085;
$night: #34495e;
$midnight: #2c3e50;
$transition: all 0.5s ease;

.toggle-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}
/* Hide the input */
.toggle [type='checkbox'] {
  position: absolute;
  opacity: 0;
  z-index: -1;
}
.toggle-trail {
  display: flex;
  align-items: center;
  width: 5rem;
  height: 2.5rem;
  background: #222;
  border-radius: 2.5em;
  transition: $transition;
  cursor: pointer;
  border: 2px solid #222;
}
.toggle-handler {
  display: flex;
  margin-left: 0.5em;
  justify-content: center;
  align-items: center;
  width: 2rem;
  height: 2rem;
  background: #fff;
  border-radius: 50%;
  transition: $transition;

  &:before {
    content: '';
    color: white;
    font-size: 2em;
    font-weight: bold;
  }
}
input[type='checkbox']:checked + .toggle-trail {
  background: transparent;
  border: 2px solid #222;
  .toggle-handler {
    margin-left: 50%;
    background: #222;
    &::before {
      content: '';
    }
  }
}
